.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

header {
  width: 100%;
  background-color: #F5F6F3;
  color: #632768;
  min-height: fit-content;
  text-align: center;
  display: flex;
  margin: 0;
  height: 100%;
  font-size: 5.5vw;
  padding: 0%;
}

div.header {
  height: 20vh;
  width: 100%;
}

div.title {
  width: 70vw;
  margin-top: 2vh;
  vertical-align: middle;
}

div.logo {
  height: 20vh;
}

text {
  align-items: center;
}

img.logo {
  display: block;
  width: 15vw;
}

div.goldspacer {
  background-color: #ffc200;
  height: 0.5vh;
  width: 100%;
}

div.navbar {
  background-color: #a691d2;
  width: 100%;
  height: 7.5vh;
}

div.narbarlinks {
  height: 100%;
}

div.navbarspace {
  height: 100%;
  background-color: #0abe22;
}

.navbar-bg {
  background: #a691d2;
  padding: 10px 0;
}

.nav-link {
  font-size: 16px;
  font-weight: bold;
  margin-right: 10px;
  color: #F5F6F3;
}

.nav-link:hover {
  color: #ffc200;
}